@font-face {
    font-family: 'Gotham';
    src: url('../fonts/GothamBlack.eot');
    src: url('../fonts/GothamBlack.eot?#iefix') format('embedded-opentype'),
        url('../fonts/GothamBlack.woff2') format('woff2'),
        url('../fonts/GothamBlack.woff') format('woff'),
        url('../fonts/GothamBlack.ttf') format('truetype'),
        url('../fonts/GothamBlack.svg#GothamBlack') format('svg');
    font-weight: 900;
    font-style: normal;
}
@import 'variables';
* {
    box-sizing: border-box;
}
body {
    font-family: $font-text;
    color: $text-color;
    
}
.clearfix::after {
    content: '';
    display: block;
    clear: both;
}
.wrap {
    width: 1200px;
    margin: 0 auto;
}
.header {
    background-color: #fff;
    &-container {
        padding: 30px 0;
    }
    &-logo {
        float: left;
        width: 50px;
    }
}
.nav {
    float: right;
}
.menu {
    &__item {
        @extend %inline-block;
    }
    &__link {
        @extend %link;
        color: #868686;
        display: block;
        padding: 12px 21px;
        transition: .3s;
        border-radius: 3px;
        font-family: $font-subtitle;
        font-size: 14px;
        font-weight: 400;
        &:hover {
            background-color: $main-color;
            color: #fff;
        } 
    }
}
.intro {
    background-color: $bg-color;
}
.business {
    padding-top: 110px;
    &-block {
        width: 900px;
        margin: 0 auto;
        text-align: center;
        color: #fff;
    }
    &__title {
        font-size: 35px;
        font-family: $font-title;
        text-transform: uppercase;
        letter-spacing: -0.88px;
        margin-bottom: 5px;
    }
    &__text {
        font-size: 16px;
        margin-bottom: 35px;
    }
}
.request {
    &__form {
        margin: 0 auto;
        width: 1150px;
    }
    &__submit {
        width: 210px;
        height: 40px;
        background-color: $main-color;
        color: #fff;
        text-transform: uppercase;
        font-size: 14px;
        outline: none;
        border: none;
        border-radius: 5px;
        transition: .3s;
        &:hover {
            cursor: pointer;
            background-color: #fdc86c;
        }
    }
    &--info {
        @extend %inline-block;
        position: relative;    
        .fas {
            position: absolute;
            left: 15px;
            top: 12px;
            font-size: 16px;
        }
    }        
}
.fa-phone {
    transform: rotateY(180deg);
}
.data {
    width: 270px;
    height: 40px;
    margin-right: 10px;
    transition: .3s;
    border-color: transparent;
    box-shadow: 0 0 2px $bg-color;
    outline: none;
    border-radius: 5px;
    color: #6e6e6e;
    font-weight: 700;
    padding: 0 5px 0 40px;
    font-size: 18px;
    &::placeholder {
        color: #a9a9a9;
        font-size: 13px;
    }
    &:focus {
        background-color: #b5d6f7;
        border-color: transparent;
        outline: none;
    }
}
.slider {
    width: 650px;
    margin: 0 auto;
    position: relative;
    top: 65px;
    &__bars {
        margin: 0 auto;
        margin-top: 25px;
        width: 100px;
        padding-left: 25px;
    }
    &__button {
        @extend %inline-block;
        width: 12px;
        height: 12px;
        border: 2px solid #dedfe0;
        border-radius: 50%;
        margin-left: 5px;
        cursor: pointer;
        &:hover {
            border-color: $bg-color; 
        }
    }
}
.title {
    font-size: 30px;
    font-family: $font-title;
    text-transform: uppercase;
    text-align: center;
    color: $title-color;    
    margin-bottom: 10px;
}
.subtitle {
    font-size: 16px;
    font-weight: 400;
    text-align: center;    
    margin-bottom: 10px;
}
.service {
    background-color: #fff;
    padding: 200px 0 150px;
    &-container {
        width: 1100px;
    }
    &-desc {
        margin-top: 80px;
        @extend %inline-block;
        width: 430px;
        position: relative;
        &:nth-child(odd) {
            margin-right: 200px;
        }
        &__title {
            font-family: $font-subtitle;
            font-weight: 700;
            font-size: 16px;
            color: $title-color;
            text-transform: uppercase;
            margin-bottom: 20px;
        }
        &__text {
            font-size: 13px;
            line-height: 22px;
        }
        .fas, .far, {
            position: absolute;
            top: 0;
            left: -80px;
            font-size: 50px;
            color: $bg-color;
            transition: .2s;
        }
        &:hover .fas,
        &:hover .far {
            color: $main-color;
        }
    }
}
.about {
    width: 100%;
    background-color: $main-color;
    background-image: url($img + 'about-bg.png');
    background-size: 50% 100%;
    background-repeat: no-repeat;
    &-info {
        float: right;
        width: 50%;
    }
    &-desc {
        color: #fff;
        width: 505px;
        padding: 170px 0 170px 80px;
        &__title {
            font-family: $font-title;
            text-transform: uppercase;
            font-size: 30px;
            margin-bottom: 30px;
        }
        &__text {
            font-size: 13px;
            line-height: 22px;
            margin-bottom: 50px;
        }
        &__link {
            @extend %link;
            display: inline-block;
            font-family: $font-subtitle;
            color: #fff;
            font-size: 14px;
            font-weight: 400;
            padding: 18px 32px;
            border: 1px solid #e7ad48;
            border-radius: 5px;
            transition: .2s;
            &:hover {
                border-color: #fff;
                box-shadow: 0 0 2px #fff;
                transform: scale(1.1);
            }
        }
    }
}
.pricing {
    background-color: #e7edf3;
    padding: 135px 0 195px;
}
.plans {
    width: 1180px;
    &-position {
        margin: 80px auto 0;        
    }
    
}
.plan {
    width: 390px;
    height: 440px;
    @extend %inline-block;
    border: 1px solid #ccd5df;
    border-radius: 3px;
    background-color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-family: $font-subtitle;
    padding-top: 55px;
    transition: .2s;
    &__title {
        font-size: 25px;
        margin-bottom: 30px;
    }
    &__price {
        font-size: 20px;
        margin-bottom: 30px;
        position: relative;
        .fas {
            position: absolute;
            color: $main-color;
            top: 5px;
            left: 140px;
            font-size: 30px;
        }
        &-number {
            font-size: 60px;
            font-weight: 700;
            color: $main-color;
            display: block;
        }
    }
    &__text {
        font-size: 14px;
        color: #b5bbc1;
        font-weight: 400;
        position: relative;
        margin-bottom: 30px;
        &::before {
            content: '';
            width: 330px;
            height: 1px;
            display: block;
            position: absolute;
            background-color: #e2e9f0;
            top: -100%;
            left: 30px;
        }
    }
    &:hover {
        transform: scale(1.1);
        background-color: $bg-color;
        color: #fff;
        .plan__text {
            color: #c2e1ff;
        }
        .plan__price-number {
            color: #fff;
        }
        .fas {
            color: #fff;
        }
    }
}
.button {
    height: 42px;
    padding: 10px 15px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 14px;
    cursor: pointer;
    background-color: $main-color;
    border: none;
    border-radius: 3px;
    display: block;
    margin: 0 auto;
    transition: .2s;
    &:hover {
        border-color: $bg-color;
        box-shadow: 0 0 2px $bg-color;
        transform: scale(1.1);
    }    
}
.work {
    padding: 150px 0;
    &-button {
        margin: 0 auto;
        width: 165px;
    }
}
.step-container {
    margin: 270px auto 90px;
}
.step {
    @extend %inline-block;
    width: 220px;
    font-size: 13px;
    margin-right: 100px;
    text-align: center;
    line-height: 22px;
    position: relative;
    &:nth-child(4n) {
        margin-right: 0;
    }
    &:nth-child(1)::before,
    &:nth-child(3)::before,
    &:nth-child(2)::after {
        content: '';
        width: 125px;
        height: 30px;
        display: block;
        background-image: url($img + 'link-step.png');
        background-size: cover;
        position: absolute;
        top: -200px;
        left: 200px;
    }
    &:nth-child(2)::after {
        transform: rotateX(180deg);
        top: -120px;
    } 

    &__title {
        font-family: $font-subtitle;
        text-transform: uppercase;
        color: $title-color;
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 30px;
    }
    .fas, .far {
        position: absolute;
        color: $bg-color;
        top: -200px;
        left: 75px;
        font-size: 72px;
    }
}
.clients {
    background-color: $main-color;
    padding: 140px 0 160px;
    &--title {
        color: #fff;
    }
    &--subtitle {
        color: #fff;
    }
}
.citation {
    padding: 50px 40px 50px 30px;
    width: 570px;
    background-color: #fff;
    margin: 0 30px 20px 0;
    @extend %inline-block;
    &:nth-child(2n) {
        margin-right: 0;
    }
    &-container {
        margin-top: 60px;
    }
    &__img {
        float: left;
    }
    &__text {
        float: right;
        width: 345px;
        font-size: 13px;
        line-height: 22px;
        position: relative;
        &--desc {
            position: relative;
            &::before,
            &::after {
                content: '';
                width: 12px;
                height: 10px;
                display: block;
                background-image: url($img + 'quote.png');
                background-size: cover;
                position: absolute;
            }
            &::before {
                top: 0;
                left: -20px;
            }
            &::after {
                transform: rotateY(180deg);
                bottom: 0;
                right: -20px;
            }
        }        
    }
    &__author {
        margin-top: 30px;
        text-transform: uppercase;
        color: #99a9ae;
        font-size: 14px;
        font-weight: 400;
        font-family: $font-subtitle;
    }
    &__name {
        color: #f6772c;
    }
}
.contacts {
    padding: 150px 0 50px;
    text-align: center;
    &-subtitle {
        margin-bottom: 40px;
    }
}
.name--contacts {
    width: 310px;
    margin-right: 10px;
}
.email--contacts {
    width: 390px;
    margin-right: 10px;
}
.map {
    margin: 55px 0;
    padding: 40px 0 160px;
    background-image: url($img + 'bg-map.png');
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::after {
        content: '';
        display: block;
        position: absolute;
        bottom: -45px;
        left: 0;
        width: 1170px;
        height: 1px;
        background-color: #e3e7ec;
    }
    &-logo {
        margin: 0 auto;
        width: 50px;
    }
}
.adress {
    margin: 0 auto;
    width: 280px;
    height: 280px;
    padding-top: 65px;
    background-color: $title-color;
    border-radius: 50%;
    font-size: 13px;
    text-align: center;
    line-height: 24px;
    color: #aeb7ba;
    margin-bottom: 15px;
    position: relative;
    &::after {
        content: '';
        width: 0;
        height: 0;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-top: 12px solid $title-color;
        position: absolute;
        top: 279px;
        left: 135px;
    }
    &__title {
        font-size: 15px;
        font-family: $font-subtitle;
        color: #fff;
        font-weight: 700;
    }
    &__link {
        text-decoration: none;
        color: $main-color;
    }
}
.footer-menu {
    width: 810px;
    margin: 70px auto 30px;
    &__link {
        color: $title-color;
        font-size: 13px;
    }
}
.footer__tel {
    @extend %link;
    font-size: 23px;
    font-family: $font-subtitle;
    color: $title-color;
    display: inline-block;
    margin-bottom: 25px;
    font-weight: 700;
    transition: .3s;
    &:hover {
        color: $main-color;
    }
}
.social {
    .fab {
        @extend %link;
        color: $bg-color;
        margin-right: 15px;
        font-size: 18px;
        transition: .3s;
        &:hover {
            color: $main-color;
        }
    }
}
.footer {
    background-color: $bg-color;
    color: #fff;
    font-size: 13px;
    text-align: center;
    padding: 20px;

}



